.receit-rowa {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-top: 30px;

  & > div {
    &:nth-child(1) {
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      width: 70%;

      div {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        column-gap: 10px;
      }
    }

    &:nth-child(2) {
      width: 30%;

      div {
        margin: 0 auto;
        width: 200px;
        height: 170px;
        background: rgba(57, 56, 56, 0.297);
        text-align: center;

        h6 {
          &:nth-child(1) {
            font-size: 0.9em;
            letter-spacing: 1px;
          }

          &:nth-child(1) {
            font-size: 1.6em;
            letter-spacing: 1.3px;
          }
        }
      }
    }
  }
}

.payment-for {
  padding-bottom: 20px;
  font-weight: bold;
  font-size: 1.4em;
}

.table-responsive {
  height: auto !important;
}

.make-bg-green {
  border-top: 12px solid var(--app-color-primary) !important;
  background: #77ad54 !important;
  height: 110px !important;
  padding: 5px !important;

  @media print {
    padding: 10px !important;
  }
}

.make-blue {
  color: blue;
}
